@import url("https://fonts.googleapis.com/css?family=Montserrat:400,700");

.about-mini {
    background-color: black;
    color: rgba(255, 255, 255, 0.767);
    padding-bottom: 5rem;
    margin-top: 0rem;
    width: 100%;
    font-family: "Montserrat", sans-serif !important;
}

.about-mini-firstRow,
.a-m-firstRow-col1,
.a-m-firstRow-col2-row1,
.a-m-firstRow-col2-row2,
.a-m-firstRow-col2-row3,
.about-mini-secondRow {
    display: flex;
    justify-content: center;
    margin: 0;
}

.a-m-firstRow-col2 {
    flex-direction: column;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 1rem 1rem;
}

.a-m-firstRow-col1 {
    background: url("https://i.ibb.co/M24XXZB/about-us-mini-background.png") no-repeat center;
    background-size: contain;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.about-us-title-decoration {
    padding: 10rem 4rem;
    border: 2px solid rgba(255, 255, 255, 0.767);
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 100%;
    box-shadow: 2px 1px 3px 1px rgb(230, 148, 86);
}

.about-us-title2 {
    font-weight: 400;
    letter-spacing: 0.3rem;
    color: rgb(230, 169, 122);
    font-size: 3rem !important;
    text-shadow: 1px 1px 3px #000000;
}

.a-m-firstRow-col2 {
    background-color: #2d3436;
    background-image: linear-gradient(315deg, #212627 0%, #000000 54%);
    display: flex;
    justify-content: center;
    align-items: center;
}

.a-m-firstRow-col2-row1,
.a-m-firstRow-col2-row2,
.a-m-firstRow-col2-row3 {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 1.2rem 0;
    width: 100%;

}

.a-m-firstRow-col2-row1,
.a-m-firstRow-col2-row2 {
    border-bottom: 0.8rem solid rgb(0, 0, 0);
    width: 100%;
}

.a-m-firstRow-col2-row1-col1,
.a-m-firstRow-col2-row2-col1,
.a-m-firstRow-col2-row3-col1 {
    border-radius: 999999px;
    box-shadow: 2px 1px 2px rgba(255, 255, 255, 0.452);
    display: flex;
    justify-content: center;
    align-items: center;
}

.about-us-mini-button {
    font-size: 0.8rem;
    font-weight: 400;
    background-color: #772f1a;
    background-image: linear-gradient(315deg, #772f1a 0%, #f2a65a 74%);
    border-radius: 99999px;
    padding: 0.8rem 6rem;
    margin: 4rem 0rem;
    color: white;
    text-shadow: 1px 1px 4px #000000;
}

.capabilities-span {
    font-size: 0.8rem;
    text-shadow: 1px 1px 3px #000000;
    padding: 0.2rem 2rem;
    border-radius: 999999px;
    margin: 0.3rem 0;
    margin-left: 0.5rem;
    background-color: #772f1a;
    background-image: linear-gradient(315deg, #772f1a 0%, #f2a65a 74%);
    display: inline-block;
}

.about-us-mini-leadership {
    font-size: 1rem;
    display: inline-block;
    text-shadow: 1px 1px 3px #000000;
}

.leadership-position {
    text-shadow: 1px 1px 3px #000000;
    font-size: 0.8rem;
    color: #727272;
}

.a-m-firstRow-col2-row1-col2,
.a-m-firstRow-col2-row2-col2,
.a-m-firstRow-col2-row3-col2 {
    display: flex;
    justify-content: center;
    padding-left: 6rem;
    flex-direction: column;
}

.a-m-firstRow-col1-container {
    display: flex;
    justify-content: center;
}

.about-us-mini-leadership {
    margin-right: 2rem;
    margin-left: 0.4rem;
}

.howWeWork-partner-sapn {
    color: #5e2716;
    text-shadow: none;
}

@media screen and (max-width: 1100px) {
    .about-mini {
        flex-direction: column;
        padding: 0;
        margin: 0;
    }

    .about-mini-firstRow,
    .a-m-firstRow-col1,
    .a-m-firstRow-col2-row1,
    .a-m-firstRow-col2-row2,
    .a-m-firstRow-col2-row3,
    .about-mini-secondRow {
        flex-direction: column;
        margin-top: 0rem;
        display: flex;
        justify-content: center;
        align-items: center;
    }

    .a-m-firstRow-col2 {
        margin: 0;
        padding: 0;
    }

    .about-us-title-decoration {
        padding: 0;
        border: none;
        box-shadow: none;
    }

    .about-us-mini-button {
        padding: 0.5rem 1rem;
        margin: 0rem;
    }

    .about-us-title2 {
        font-weight: 400;
        letter-spacing: 0.1rem;
        font-size: 2rem !important;
    }

    .a-m-firstRow-col2 {
        padding: 1.5rem 0rem;
    }

    .a-m-firstRow-col2-row1,
    .a-m-firstRow-col2-row2,
    .a-m-firstRow-col2-row3 {
        display: flex;
        justify-content: center;
        align-items: center;
        padding: 0rem;
        margin: 0;
        width: 100%;
        text-align: center;
        padding: 0 2rem;

    }

    .a-m-firstRow-col2-row1-col1,
    .a-m-firstRow-col2-row2-col1,
    .a-m-firstRow-col2-row3-col1 {
        margin: 4rem 1rem;
        box-shadow: 2px 2px 2px 2px rgba(255, 255, 255, 0.452);
        display: flex;
        justify-content: center;
        align-items: center;
        text-align: center;
        padding: 0;
    }

    .capabilities-span {
        width: 30%;
        display: inline-block;
        font-size: 0.5rem;
        padding: 0.1rem 0rem;
        margin: 0.1rem 0;
        margin-left: 0.2rem;
    }

    .a-m-firstRow-col2-row1-col2,
    .a-m-firstRow-col2-row2-col2 {
        display: flex;
        justify-content: center;
        padding-left: 0rem;
    }

    .a-m-firstRow-col2-row3-col2 {
        flex-direction: row;
        padding-left: 0rem;
    }
}