.home-section {
    padding-bottom: 11.25rem;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    z-index: 2;
}

.home-title {
    font-family: "Montserrat", sans-serif;
    text-align: center;
    padding-top: 11rem !important;
    padding-bottom: 1rem !important;
    color: rgba(255, 255, 255, 0.918);
    font-size: 3.5rem;
    font-weight: 600;
    padding: 0 1rem;
}

.home-subtitle-col {
    display: flex;
    justify-content: center;
    align-items: center;
}

.home-subtitle {
    font-family: "Montserrat", sans-serif;
    text-align: center;
    color: rgba(216, 216, 216, 0.904);
    font-size: 0.8rem;
    max-width: 40rem;
    padding: 0;
}

.aboutLink {
    text-decoration: none !important;
    color: white !important;
}

/*-------------------------------------- HOME SECTION ending------------------------------------------------*/
/*-------------------------------------- MEDIA QUERIES------------------------------------------------*/

@media screen and (max-width: 1000px) {
    .home {
        background: url("https://i.ibb.co/P6VMvXr/home-Background2-1.jpg") no-repeat bottom;
        background-size: cover;
    }

    .home-title {
        padding-top: 5rem !important;
        font-size: 2rem;
    }

    .home-section {
        padding-bottom: 0rem;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        padding-bottom: 3rem !important;
    }

    .home-subtitle {
        font-size: 0.8rem !important;
        max-width: 20rem !important;
        padding: 1rem 1.5rem;

    }
}

@media screen and (min-width: 1000px) {}