@import url("https://fonts.googleapis.com/css?family=Montserrat:400,700");
@import url('https://fonts.googleapis.com/css2?family=Julius+Sans+One&family=Megrim&family=Parisienne&display=swap');

.footer {
    background: url("https://i.ibb.co/7kRDpKc/footer-Background2-2.jpg") no-repeat top;
    background-size: cover;
    padding-top: 20rem;
    font-size: 0.9rem;
    font-family: "Montserrat", sans-serif;
    color: rgba(255, 255, 255, 0.856);
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.mdbContainer {
    text-align: center;
    display: flex;
    align-items: center;
    justify-self: center;
    flex-direction: column;
}

.mdb-row {
    text-align: center;
    display: flex;
    align-items: center;
    justify-self: center;
    flex-direction: column;
}

.socialMedia-div {
    text-align: center;
    display: flex;
    align-items: center;
    justify-self: center;
    flex-direction: row;
}

.socialMedia-div>a {
    padding-right: 0.5rem;
}

.mdb-links {
    text-align: center;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: row;
}


.MuiSvgIcon-root {
    color: rgba(255, 255, 255, 0.623);
}

.a-copyrights {
    font-weight: 600;
    color: #202222 !important;
    text-decoration: none !important;
}

.footer-firstRow {
    padding: 0.5rem 2rem;
}

.separatorline-row {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0.09rem 0;
    display: flex;
    background-color: #2d3436;
    background-image: linear-gradient(315deg, #212627 0%, #000000 54%);
    width: 80%;
}

.footer-secondRow {
    padding: 0.5rem 0;
    display: flex;
    align-items: center;
    justify-content: center;
}

.footer-firstRow,
.footer-firstRow-col1,
.footer-firstRow-col2 {
    display: flex;
    align-items: center;
    justify-content: center;
}

.footer-firstRow-col2 {
    display: inline-block;
    flex-direction: column;
    background-color: #2d3436;
    background-image: linear-gradient(315deg, #212627 0%, #000000 54%);
    border-radius: 9999px;
    border-bottom-left-radius: 100%;
    padding: 1rem 3rem;
}


.footer-subscribe-header {
    font-size: 0.9rem;
}

.footer-subscribe-subtitle {
    font-size: 0.8rem;
}

.footer-firstRow-innerRow {
    display: flex;
    align-items: flex-start;
    justify-content: center;
    padding: 1rem 0rem;
}

.f-f-cols-1 {
    display: flex;
    align-items: flex-start;
    justify-content: flex-start;
    flex-direction: column;
}

.footer-topLink {
    font-size: 0.85rem;
}

.footer-topLink-underline {
    width: 90%;
    padding: 0.1rem 0;
    background-color: #2d3436;
    background-image: linear-gradient(315deg, #212627 0%, #000000 54%);
}

.f-f-cols-1-links-div {
    display: flex;
    align-items: flex-start;
    justify-content: flex-start;
    flex-direction: column;
    padding: 0.8rem 0;
    font-size: 0.85rem;
    color: rgba(245, 245, 245, 0.623);
}

.f-f-cols-1-links-div>a {
    padding: 0.2rem 0;
}

.footer-Link,
.footer-Link:hover {
    color: rgba(245, 245, 245, 0.623);
    text-decoration: none !important;
}

.footer-Link:hover {
    color: rgba(245, 245, 245, 0.423);
}

.footer-firstRow-col2-row1 {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    padding: 1rem 0;
    text-align: center;
}

.footer-firstRow-col2-1 {
    display: contents;
}

.footer-input {
    width: 60%;
}

.footer-firstRow-col2-row2 {
    display: flex !important;
    justify-content: flex-start !important;
    align-items: flex-end !important;
    padding: 1rem 0;
}

.footer-button {
    color: #AD8F72 !important;
    border: 1px solid rgba(245, 245, 245, 0.534) !important;
    padding: 0.05rem 3rem !important;

}

.socialMedia-div {
    display: flex;
    align-items: center;
    justify-content: flex-start;
}

.icon-inner-div {
    display: flex;
    align-items: center;
    justify-content: center;
    border: 2px solid #AD8F72;
    border-radius: 100%;
    padding: 0.4rem 0.4rem;
    margin-right: 2rem;
    margin-top: 1rem;
}

.itpeaks-brand-name {
    font-family: 'Megrim', cursive;
    font-size: 2rem;
    color: #833b2b;
    font-weight: 600;
    padding: 0.4rem 2rem;
    border-radius: 99999px;
    text-decoration: none !important;
    letter-spacing: 0.1rem;
    background-color: #2d3436;
    background-image: linear-gradient(315deg, #212627 0%, #000000 54%);
}

.itpeaks-brand-name:hover {
    color: #74392c;
}

@media screen and (max-width: 1100px) {
    .footer-topLink {
        font-size: 0.6rem;
        padding: 0;
        margin: 0;
    }

    .f-f-cols-1-links-div {
        text-align: left;
        font-size: 0.5rem;
        padding: 0;
        margin: 0;
    }

    .footer-subscribe-header {
        font-size: 0.6rem;
        padding: 0;
        margin: 0;
    }

    .footer-subscribe-subtitle {
        font-size: 0.5rem;
        padding: 0;
        margin: 0;
    }

    .footer-firstRow-col2 {
        padding: 0;
        margin: 0;
    }

    .footer-firstRow-col2-row2,
    .footer-firstRow-col2-2 {
        padding: 0;
        margin: 0;
    }

    .footer-input,
    .footer-firstRow-col2-1,
    .footer-button {
        display: flex;
        align-items: center;
        justify-content: center;
        padding: 0;
        margin: 0;
        width: 70%;
    }

    .footer-firstRow-col2-row3 {
        display: flex;
        align-items: center;
        justify-content: center;
        padding: 0;
        margin: 0;
    }

    .footer-firstRow-col2-3,
    .socialMedia-div,
    .icon-inner-div,
    .icon-link,
    .icon-inner-div {
        display: flex;
        align-items: center;
        justify-content: center;
        padding: 0;
        margin: 0.1rem 0;
        font-size: 0.5rem;
        border: none;
    }

    .footer-copyright {
        font-size: 0.5rem;
    }

    .itpeaks-brand-name {
        font-size: 1rem;
    }
}